import React from 'react';
import LoginInputs from './LoginInputs';
import './Login.css';
import Or from '../Or'



function Login(props) {
    return (
      <section>
        <div className="signinpart">
            <div className="iconpart"><i className="fa fa-times"></i></div>
            <h2>Sign In</h2>
            <form>
                <LoginInputs />
                <button value="Login" name="action" className="firstbutton">Sign in</button>
                <Or />
                <button onClick={props.onClick}  className="secondbutton" ><a>Create an account</a> </button>
            </form>
        </div>
    </section>
                              
    );
  }


export default Login
