import React from 'react';
import './Register.css';
import RegisterInputs from './RegisterInputs';
import Or from '../Or'


function Register(props) {
    return (
      <section>
            <div className="registrationpart">
                <div className="iconpart"><i className="fa fa-times"></i></div>
                    <h2>Registration</h2>
                <form>
                    <RegisterInputs />
                    <button name="btnone" value="btn1" id="btnbtn" className="firstbutton">Registration</button>
                    <Or />
                    <button  onClick={props.onClick}  className="secondbutton" ><a href="#">Login</a></button>
                    </form>
                </div>
          </section>
    );
  }

export default Register
